import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';
import { RootState } from 'redux/store';
import { movieListByPageId } from 'services/genreService';
import { MovieListResponse } from '../types';
import { createAsyncThunk } from '@reduxjs/toolkit';
import { Page } from '../types';
import { Content } from '../types/index';

interface GenreMovieState {
	currentPage: number;
	page: Page | null;
	loading: boolean;
	error: string | null;
	movieList: Content[];
	mainMovieList: Content[];
	title: string;
	isSearchOpen: boolean;
	searchQuery: string;
}

const initialState: GenreMovieState = {
	currentPage: 1,
	page: null,
	loading: false,
	error: null,
	movieList: [],
	mainMovieList: [],
	title: '',
	isSearchOpen: false,
	searchQuery: '',
};

export const genreMovieSlice = createSlice({
	name: 'genreMovie',
	initialState,
	reducers: {
		setPage: (state, action: PayloadAction<Page>) => {
			state.page = action.payload;
		},
		setLoading: (state, action: PayloadAction<boolean>) => {
			state.loading = action.payload;
		},
		setError: (state, action: PayloadAction<string | null>) => {
			state.error = action.payload;
		},
		toggleSearch: (state) => {
			state.isSearchOpen = !state.isSearchOpen;
			if (!state.isSearchOpen) {
				state.searchQuery = '';
			}
		},
		updateSearchQuery: (state, action) => {
			state.searchQuery = action.payload;
		},
		updateMovieList: (state, action) => {
			state.searchQuery = action.payload;
			if (state.searchQuery === '') {
				state.movieList = [...state.mainMovieList];
			} else {
				state.movieList = state.mainMovieList.filter((movie) =>
					movie.name
						.toLowerCase()
						.includes(state.searchQuery.toLowerCase())
				);
			}
		},
	},
	extraReducers: (builder) => {
		builder
			.addCase(fetchMovieList.pending, (state) => {
				state.loading = true;
				state.error = null;
			})
			.addCase(fetchMovieList.fulfilled, (state, action) => {
				const page = action.payload?.page;
				state.loading = false;
				state.title = page?.title;
				if (!page || !page['content-items']?.content) {
					state.error =
						'Sorry, no movies were found at the moment. Please try again later.';
					return;
				}
				state.mainMovieList = [
					...state.mainMovieList,
					...page['content-items']?.content,
				];
				if (state.searchQuery) {
					state.movieList = state.mainMovieList.filter((movie) =>
						movie.name
							.toLowerCase()
							.includes(state.searchQuery.toLowerCase())
					);
				} else {
					state.movieList = [...state.mainMovieList];
				}

				state.currentPage = parseInt(page['page-num-requested'], 10);
			})
			.addCase(fetchMovieList.rejected, (state, action) => {
				state.loading = false;
				state.error =
					'Sorry, no movies were found at the moment. Please try again later.';
			});
	},
});

export const {
	setPage,
	setLoading,
	setError,
	toggleSearch,
	updateSearchQuery,
	updateMovieList,
} = genreMovieSlice.actions;

// Other code such as selectors can use the imported `RootState` type
export const selectCount = (state: RootState) =>
	state.genreMovie.page;

// Define the async action creator
export const fetchMovieList = createAsyncThunk(
	'romanticComedy/fetchMovieList',
	async (pageNumber: number) => {
		// Replace this with your API call logic
		const response = await movieListByPageId(pageNumber);
		return response as MovieListResponse;
	}
);

export default genreMovieSlice.reducer;
