import { MovieListResponse } from 'features/GenreMovieList';
import { axios } from 'lib/axios';
import { SERVICE_INFO } from 'config';

export const movieListByPageId= async (
	pageNumber: number
): Promise<MovieListResponse>  => {
		const url = SERVICE_INFO.apis.movieList + pageNumber + '.json';
		return await axios.get(url);
};
