import { lazyImport } from 'utils/lazyImport';
import { Navigate } from 'react-router-dom';

const { GenreMovieRoutes } = lazyImport(
	() => import('features/GenreMovieList'),
	'GenreMovieRoutes'
);

export const publicRoutes = [
	{
		path: '/diagnalStatic/*',
		element: <GenreMovieRoutes />,
	},
	{
		path: '/*',
		element: <Navigate to="/diagnalStatic" />,
	},
];
