import ClipLoader from 'react-spinners/ClipLoader';
import { useTranslation } from 'react-i18next';

import './Spinner.scss';

export const Spinner = () => {
	const { t } = useTranslation();

	return (
		<section className="suspense-spinner">
			<ClipLoader
				color={'#FFFFFF'}
				loading={true}
				size={100}
				aria-label={t('spinnerLabel')}
			/>
		</section>
	);
};
